import { StockData } from "./stockApi";

export interface PredictionResult {
  day: string;
  predictedPrice: number;
  confidence: number;
  trend: 'bullish' | 'bearish' | 'neutral';
}

export interface TechnicalIndicators {
  rsi: number;
  movingAverage: number;
  volatility: number;
  momentum: number;
}

export function calculateTechnicalIndicators(stockData: StockData): TechnicalIndicators {
  const { regularMarketPrice, regularMarketOpen, regularMarketDayHigh, regularMarketDayLow } = stockData;
  
  // Simplified RSI calculation (normally requires 14 days of data)
  const priceChange = regularMarketPrice - regularMarketOpen;
  const rsi = 50 + (priceChange / regularMarketPrice) * 100;
  
  // Simple moving average approximation
  const movingAverage = (regularMarketDayHigh + regularMarketDayLow + regularMarketPrice) / 3;
  
  // Volatility based on day's range
  const volatility = ((regularMarketDayHigh - regularMarketDayLow) / regularMarketPrice) * 100;
  
  // Momentum indicator
  const momentum = ((regularMarketPrice - regularMarketOpen) / regularMarketOpen) * 100;
  
  return {
    rsi: Math.max(0, Math.min(100, rsi)),
    movingAverage,
    volatility,
    momentum
  };
}

export function simulatePrediction(stockData: StockData): PredictionResult[] {
  const results: PredictionResult[] = [];
  const basePrice = stockData.regularMarketPrice;
  const indicators = calculateTechnicalIndicators(stockData);
  
  // Base trend determination
  let baseTrend = 0;
  if (indicators.rsi > 70) baseTrend = -0.5; // Overbought
  else if (indicators.rsi < 30) baseTrend = 0.5; // Oversold
  else baseTrend = indicators.momentum / 100;
  
  // Generate predictions for the next 7 days
  let currentPrice = basePrice;
  
  for (let i = 1; i <= 7; i++) {
    // Apply trend with some randomness
    const trendFactor = baseTrend * (1 - i * 0.1); // Trend weakens over time
    const randomFactor = (Math.random() - 0.5) * 0.02; // Â±1% random variation
    const volatilityFactor = (indicators.volatility / 100) * (Math.random() - 0.5) * 0.01;
    
    const totalChange = trendFactor + randomFactor + volatilityFactor;
    currentPrice = currentPrice * (1 + totalChange);
    
    // Determine trend direction
    let trend: 'bullish' | 'bearish' | 'neutral' = 'neutral';
    if (currentPrice > basePrice * 1.02) trend = 'bullish';
    else if (currentPrice < basePrice * 0.98) trend = 'bearish';
    
    // Calculate confidence based on various factors
    const confidence = Math.max(60, Math.min(95, 
      80 - (indicators.volatility * 2) + (Math.abs(indicators.momentum) * 0.5)
    ));
    
    results.push({
      day: `Day ${i}`,
      predictedPrice: parseFloat(currentPrice.toFixed(2)),
      confidence: parseFloat(confidence.toFixed(1)),
      trend
    });
  }
  
  return results;
}

export function generateOptionsStrategy(stockData: StockData, predictions: PredictionResult[]): {
  strategy: string;
  reasoning: string;
  riskLevel: 'Low' | 'Medium' | 'High';
  expectedReturn: string;
} {
  const currentPrice = stockData.regularMarketPrice;
  const lastPrediction = predictions[predictions.length - 1];
  const priceChange = ((lastPrediction.predictedPrice - currentPrice) / currentPrice) * 100;
  const indicators = calculateTechnicalIndicators(stockData);
  
  let strategy = '';
  let reasoning = '';
  let riskLevel: 'Low' | 'Medium' | 'High' = 'Medium';
  let expectedReturn = '';
  
  if (priceChange > 3 && indicators.rsi < 70) {
    strategy = 'Buy Call Options';
    reasoning = 'Strong upward trend predicted with good momentum. RSI indicates room for growth.';
    riskLevel = indicators.volatility > 5 ? 'High' : 'Medium';
    expectedReturn = '15-25%';
  } else if (priceChange < -3 && indicators.rsi > 30) {
    strategy = 'Buy Put Options';
    reasoning = 'Downward trend predicted. RSI suggests further decline possible.';
    riskLevel = indicators.volatility > 5 ? 'High' : 'Medium';
    expectedReturn = '10-20%';
  } else if (Math.abs(priceChange) < 2) {
    strategy = 'Iron Condor Strategy';
    reasoning = 'Price expected to remain stable. Profit from low volatility.';
    riskLevel = 'Low';
    expectedReturn = '5-10%';
  } else if (indicators.volatility > 8) {
    strategy = 'Straddle Strategy';
    reasoning = 'High volatility detected. Profit from large price movements in either direction.';
    riskLevel = 'High';
    expectedReturn = '20-40%';
  } else {
    strategy = 'Hold Position';
    reasoning = 'Mixed signals. Consider waiting for clearer market direction.';
    riskLevel = 'Low';
    expectedReturn = '0-5%';
  }
  
  return { strategy, reasoning, riskLevel, expectedReturn };
}
