"use client";

import React, { useState } from "react";
import { fetchStockData, StockData } from "@/lib/stockApi";
import { simulatePrediction, PredictionResult, generateOptionsStrategy } from "@/lib/predictionSimulator";
import PredictionResults from "./PredictionResults";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";

export default function StockPrediction() {
  const [ticker, setTicker] = useState("");
  const [stockData, setStockData] = useState<StockData | null>(null);
  const [predictions, setPredictions] = useState<PredictionResult[]>([]);
  const [optionsStrategy, setOptionsStrategy] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!ticker.trim()) {
      setError("Please enter a valid stock ticker symbol.");
      return;
    }

    setLoading(true);
    setError(null);
    setStockData(null);
    setPredictions([]);
    setOptionsStrategy(null);

    try {
      const data = await fetchStockData(ticker.trim().toUpperCase());
      setStockData(data);
      
      const predictionResults = simulatePrediction(data);
      setPredictions(predictionResults);
      
      const strategy = generateOptionsStrategy(data, predictionResults);
      setOptionsStrategy(strategy);
    } catch (err: any) {
      setError(err.message || "An error occurred while fetching data.");
    } finally {
      setLoading(false);
    }
  };

  const popularStocks = ['AAPL', 'GOOGL', 'MSFT', 'TSLA', 'AMZN', 'NVDA', 'META', 'NFLX'];

  return (
    <div className="min-h-screen bg-background p-4">
      <div className="max-w-6xl mx-auto space-y-6">
        <Card>
          <CardHeader className="text-center">
            <CardTitle className="text-3xl font-bold">Stock Market Prediction</CardTitle>
            <CardDescription className="text-lg">
              Get AI-powered stock predictions and options trading insights
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSearch} className="space-y-4">
              <div className="flex flex-col sm:flex-row gap-4">
                <div className="flex-1">
                  <Input
                    type="text"
                    value={ticker}
                    onChange={(e) => setTicker(e.target.value.toUpperCase())}
                    placeholder="Enter stock ticker (e.g., AAPL)"
                    className="text-lg h-12"
                  />
                </div>
                <Button 
                  type="submit" 
                  disabled={loading}
                  className="h-12 px-8 text-lg"
                >
                  {loading ? "Analyzing..." : "Predict"}
                </Button>
              </div>
            </form>

            <div className="mt-6">
              <p className="text-sm text-muted-foreground mb-3">Popular stocks:</p>
              <div className="flex flex-wrap gap-2">
                {popularStocks.map((stock) => (
                  <Button
                    key={stock}
                    variant="outline"
                    size="sm"
                    onClick={() => setTicker(stock)}
                    className="text-xs"
                  >
                    {stock}
                  </Button>
                ))}
              </div>
            </div>

            {error && (
              <Alert className="mt-4 border-destructive">
                <AlertDescription className="text-destructive">
                  {error}
                </AlertDescription>
              </Alert>
            )}
          </CardContent>
        </Card>

        {loading && (
          <Card>
            <CardContent className="p-8">
              <div className="flex items-center justify-center space-x-2">
                <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-primary"></div>
                <p className="text-lg">Analyzing market data and generating predictions...</p>
              </div>
            </CardContent>
          </Card>
        )}

        {!loading && stockData && predictions.length > 0 && (
          <PredictionResults 
            stockData={stockData} 
            predictions={predictions}
            optionsStrategy={optionsStrategy}
          />
        )}
      </div>
    </div>
  );
}
