import React from "react";
import { StockData } from "@/lib/stockApi";
import { PredictionResult, calculateTechnicalIndicators } from "@/lib/predictionSimulator";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Progress } from "@/components/ui/progress";

interface PredictionResultsProps {
  stockData: StockData;
  predictions: PredictionResult[];
  optionsStrategy: {
    strategy: string;
    reasoning: string;
    riskLevel: 'Low' | 'Medium' | 'High';
    expectedReturn: string;
  };
}

export default function PredictionResults({ stockData, predictions, optionsStrategy }: PredictionResultsProps) {
  const indicators = calculateTechnicalIndicators(stockData);
  const currentPrice = stockData.regularMarketPrice;
  const changePercent = stockData.regularMarketChangePercent;
  const lastPrediction = predictions[predictions.length - 1];
  const predictedChange = ((lastPrediction.predictedPrice - currentPrice) / currentPrice) * 100;

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(value);
  };

  const formatNumber = (value: number) => {
    return new Intl.NumberFormat('en-US').format(value);
  };

  const getRiskColor = (risk: string) => {
    switch (risk) {
      case 'Low': return 'bg-green-500';
      case 'Medium': return 'bg-yellow-500';
      case 'High': return 'bg-red-500';
      default: return 'bg-gray-500';
    }
  };

  const getTrendColor = (trend: string) => {
    switch (trend) {
      case 'bullish': return 'text-green-600';
      case 'bearish': return 'text-red-600';
      default: return 'text-gray-600';
    }
  };

  return (
    <div className="space-y-6">
      {/* Current Stock Data */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span>{stockData.symbol} - Current Data</span>
            <Badge variant={changePercent >= 0 ? "default" : "destructive"}>
              {changePercent >= 0 ? "+" : ""}{changePercent.toFixed(2)}%
            </Badge>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center">
              <p className="text-2xl font-bold">{formatCurrency(currentPrice)}</p>
              <p className="text-sm text-muted-foreground">Current Price</p>
            </div>
            <div className="text-center">
              <p className="text-lg font-semibold">{formatCurrency(stockData.regularMarketOpen)}</p>
              <p className="text-sm text-muted-foreground">Open</p>
            </div>
            <div className="text-center">
              <p className="text-lg font-semibold">{formatCurrency(stockData.regularMarketDayHigh)}</p>
              <p className="text-sm text-muted-foreground">Day High</p>
            </div>
            <div className="text-center">
              <p className="text-lg font-semibold">{formatCurrency(stockData.regularMarketDayLow)}</p>
              <p className="text-sm text-muted-foreground">Day Low</p>
            </div>
          </div>
          
          <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <p className="text-sm text-muted-foreground mb-1">Volume</p>
              <p className="text-lg font-semibold">{formatNumber(stockData.regularMarketVolume)}</p>
            </div>
            <div>
              <p className="text-sm text-muted-foreground mb-1">Market Cap</p>
              <p className="text-lg font-semibold">{formatCurrency(stockData.marketCap)}</p>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Technical Indicators */}
      <Card>
        <CardHeader>
          <CardTitle>Technical Indicators</CardTitle>
          <CardDescription>Key metrics for technical analysis</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            <div>
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm font-medium">RSI</span>
                <span className="text-sm">{indicators.rsi.toFixed(1)}</span>
              </div>
              <Progress value={indicators.rsi} className="h-2" />
              <p className="text-xs text-muted-foreground mt-1">
                {indicators.rsi > 70 ? "Overbought" : indicators.rsi < 30 ? "Oversold" : "Neutral"}
              </p>
            </div>
            
            <div>
              <p className="text-sm font-medium">Moving Average</p>
              <p className="text-lg font-bold">{formatCurrency(indicators.movingAverage)}</p>
              <p className="text-xs text-muted-foreground">3-period MA</p>
            </div>
            
            <div>
              <p className="text-sm font-medium">Volatility</p>
              <p className="text-lg font-bold">{indicators.volatility.toFixed(2)}%</p>
              <p className="text-xs text-muted-foreground">Daily range</p>
            </div>
            
            <div>
              <p className="text-sm font-medium">Momentum</p>
              <p className={`text-lg font-bold ${indicators.momentum >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                {indicators.momentum >= 0 ? "+" : ""}{indicators.momentum.toFixed(2)}%
              </p>
              <p className="text-xs text-muted-foreground">Price momentum</p>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Predictions */}
      <Card>
        <CardHeader>
          <CardTitle>7-Day Price Predictions</CardTitle>
          <CardDescription>
            AI-generated predictions based on technical analysis
            <span className={`ml-2 font-semibold ${predictedChange >= 0 ? 'text-green-600' : 'text-red-600'}`}>
              Expected: {predictedChange >= 0 ? "+" : ""}{predictedChange.toFixed(2)}%
            </span>
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Day</TableHead>
                <TableHead>Predicted Price</TableHead>
                <TableHead>Change</TableHead>
                <TableHead>Confidence</TableHead>
                <TableHead>Trend</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {predictions.map((prediction, index) => {
                const change = ((prediction.predictedPrice - currentPrice) / currentPrice) * 100;
                return (
                  <TableRow key={index}>
                    <TableCell className="font-medium">{prediction.day}</TableCell>
                    <TableCell className="font-semibold">
                      {formatCurrency(prediction.predictedPrice)}
                    </TableCell>
                    <TableCell className={change >= 0 ? 'text-green-600' : 'text-red-600'}>
                      {change >= 0 ? "+" : ""}{change.toFixed(2)}%
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center space-x-2">
                        <Progress value={prediction.confidence} className="w-16 h-2" />
                        <span className="text-sm">{prediction.confidence}%</span>
                      </div>
                    </TableCell>
                    <TableCell>
                      <Badge 
                        variant="outline" 
                        className={getTrendColor(prediction.trend)}
                      >
                        {prediction.trend}
                      </Badge>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {/* Options Strategy */}
      <Card>
        <CardHeader>
          <CardTitle>Options Trading Strategy</CardTitle>
          <CardDescription>Recommended strategy based on predictions</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h3 className="text-xl font-semibold">{optionsStrategy.strategy}</h3>
              <div className="flex items-center space-x-2">
                <span className="text-sm">Risk Level:</span>
                <Badge className={getRiskColor(optionsStrategy.riskLevel)}>
                  {optionsStrategy.riskLevel}
                </Badge>
              </div>
            </div>
            
            <p className="text-muted-foreground">{optionsStrategy.reasoning}</p>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
              <div className="p-4 bg-muted rounded-lg">
                <p className="font-medium">Expected Return</p>
                <p className="text-2xl font-bold text-green-600">{optionsStrategy.expectedReturn}</p>
              </div>
              <div className="p-4 bg-muted rounded-lg">
                <p className="font-medium">Risk Assessment</p>
                <p className="text-lg font-semibold">{optionsStrategy.riskLevel} Risk</p>
              </div>
            </div>
            
            <div className="mt-4 p-4 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg border border-yellow-200 dark:border-yellow-800">
              <p className="text-sm text-yellow-800 dark:text-yellow-200">
                <strong>Disclaimer:</strong> This is for educational purposes only. Options trading involves significant risk. 
                Always consult with a financial advisor before making investment decisions.
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
