export interface StockData {
  symbol: string;
  regularMarketPrice: number;
  regularMarketChangePercent: number;
  regularMarketOpen: number;
  regularMarketDayHigh: number;
  regularMarketDayLow: number;
  regularMarketVolume: number;
  marketCap: number;
}

export async function fetchStockData(ticker: string): Promise<StockData> {
  try {
    const response = await fetch(`/api/stock?ticker=${ticker}`);
    
    if (!response.ok) {
      throw new Error("Failed to fetch stock data from server.");
    }
    
    const data = await response.json();
    
    if (data.error) {
      throw new Error(data.error);
    }
    
    return {
      symbol: data.symbol,
      regularMarketPrice: data.regularMarketPrice,
      regularMarketChangePercent: data.regularMarketChangePercent,
      regularMarketOpen: data.regularMarketOpen,
      regularMarketDayHigh: data.regularMarketDayHigh,
      regularMarketDayLow: data.regularMarketDayLow,
      regularMarketVolume: data.regularMarketVolume,
      marketCap: data.marketCap,
    };
  } catch (error) {
    console.error("Error fetching stock data:", error);
    throw new Error("Failed to fetch stock data. Please check the ticker symbol and try again.");
  }
}
